<template>
 <div class="container">
  <el-card >
  <div slot="header">
    <strong></strong>
  </div>
    <el-form class="mt20" ref="form" :rules="rules" :model="form" label-width="200px" label-position="top" label-suffix=":">
      <el-form-item label="" prop="login_name">
        <el-input v-model.number="form.login_name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="pwd">
        <el-input type="password" v-model="form.pwd" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="nickname">
        <el-input v-model="form.nickname" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="sex">
         <el-radio-group v-model="form.sex" style="width: 100%;" placeholder="">
           <el-radio label=""></el-radio>
           <el-radio label=""></el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- <el-form-item label="" prop="contact">
        <div class="form-description">qqeg. zxzmls()</div>
        <el-input v-model="form.contact" placeholder=""></el-input>
      </el-form-item> -->
      <el-form-item label="" prop="date">
        <div class="form-description"></div>
        <el-col :span="11">
          <el-date-picker
            type="date"
            value-format="yyyy-MM-dd"
            placeholder=""
            v-model="form.date"
            style="width: 100%;"
          ></el-date-picker>
        </el-col>
        <el-col class="tr" :span="2">-</el-col>
        <el-col :span="11">
          <el-time-picker
            placeholder=""
            value-format="HH:mm:ss"
            v-model="form.time"
            style="width: 100%;"
          ></el-time-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="()" prop="city">
        <el-input v-model="form.city" placeholder=""></el-input>
      </el-form-item>
      <!-- <el-form-item label="" prop="marriage">
        <el-radio-group v-model="form.marriage" style="width: 100%;">
           <el-radio label=""></el-radio>
           <el-radio label=""></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" prop="job">
        <div class="form-description"></div>
        <el-input v-model="form.job" placeholder=""></el-input>
      </el-form-item> -->
      <div class="tr mt40">
        <el-button type="primary" style="width: 100%;" @click="onSubmit"></el-button>
      </div>
    </el-form>
  </el-card>
 </div>
</template>

<script>
import { postAction } from '@/api/manage'
  export default {
    data() {
      return {
        form: {
          login_name: '',
          pwd: '',
          nickname: '',
          sex: '',
          // contact: '',
          date: '',
          time:'',
          city: '',
          // marriage: '',
          // job: ''
        },
        rules: {
          login_name: [{required: true, message:"", trigger: 'blur' }],
          pwd: [{required: true, message:"", trigger: 'blur' }],
          nickname: [{required: true, message:"", trigger: 'blur' }],
          sex: [{required: true, message:"", trigger: 'blur' }],
          // contact: [{required: true, message:"", trigger: 'blur' }],
          date: [{required: true, message:"", trigger: 'blur' }],
          time: [{required: true, message:"", trigger: 'blur' }],
          city: [{required: true, message:"", trigger: 'blur' }]
          // job: [{required: true, message:"", trigger: 'blur' }]
        }
      };
    },
    methods: {
      onSubmit() {
        const url = '/user/register'
        this.$refs.form.validate((valid) => {
          if (valid) {
            const date = this.form.date
            const time = this.form.time
            this.form.year = date.split("-")[0]
            this.form.month = date.split("-")[1]
            this.form.day = date.split("-")[2]
            this.form.hour = date.split(":")[0]
            this.form.minute = date.split(":")[1]
            postAction( url, this.form).then( res => {
                if( res.data.code == 200) {
                  this.$message.success(res.data.msg)
                  this.$router.push({name: 'Login'})
                }
                else{
                  this.$message.warning(res.data.msg)
                }
            })

          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
    }
  }
</script>
<style >
.container {
   text-align: justify;
   width: 100%;
   max-width: 800px;
   margin-right: auto;
   margin-left: auto;
   /* padding-left: 16px;
   padding-right: 16px; */
}
.el-form-item__label{
  font-weight: bold;
  font-size: 1em ;
}
.el-form--label-top .el-form-item__label {
    float: none;
    display: inline-block;
    text-align: left;
    padding: 0px;
}
.el-form-item__content{
  font-size: 0.95em ;
}
.title{
  color: #409EFF;
  text-align: center;
  margin: 16px 0px;
}
.tr{
  text-align: center;
}
.mt20{
  margin-top: 24px;
}
.mt40{
  margin-top: 40px;
}
.form-description{
  color: rgba(0,0,0,0.6);
  font-size:0.9em;
  line-height: 1.75em;
  padding: 2px;
}
</style>
